import interfaceurl from '@/utils/interface.js'
import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
import cityData from '@/common/city.data.js';
export default {
	components: {
		mpvuePicker,
		mpvueCityPicker
	},
	data() {
		return {
			//动态参数
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 1],
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0],
			pickerValueArray:[],
			saveParams: {
				detail: '', //详细地址
				address: '',
				contact_name: '',
				contact_mobile: ''
			},
			cart_id: '' //购物车id
		}
	},
	onLoad(options) {
		this.cart_id = options.cart_id
	},
	onShow() {
     
	},
	methods: {
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			//console.log(e)
			this.saveParams.address = e.label.replace('-', '').replace('-', '');
		},
		onCancel(e) {
			//console.log('取消地址选择')
		},
		switchChange (val) {
			this.saveParams.isDefault = val.detail.value ? 1 : 0
		},
		// updateAddress() {
		// 	let that = this;
		// 	interfaceurl.checkAuth(interfaceurl.addressUpdate, that.saveParams, false).then((res) => {
		// 		//保存成功后关闭本页面跳到上一页
		// 		that.$turnPage('1', 'navigateBack')
		// 	});
		// },
		saveAddress() {
			if(!this.saveParams.contact_name){
			  uni.showToast({
				 title: '请填写收货人',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(!this.saveParams.contact_mobile){
			  uni.showToast({
				 title: '请填写收货人手机号码',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(!this.saveParams.address){
			  uni.showToast({
				 title: '请选择地址',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			if(!this.saveParams.detail){
			  uni.showToast({
				 title: '请填写详细地址',
				 icon: 'none',
				 duration: 2000
			  });
			  return
			}
			let params = {
				address: this.saveParams.address + this.saveParams.detail,
				contact_name: this.saveParams.contact_name,
				contact_mobile: this.saveParams.contact_mobile
			}
			// if(this.saveParams.addressId){
			//   this.updateAddress()
			//   return;
			// }
			let that = this;
			interfaceurl.checkAuth(interfaceurl.addressCreate, params).then((res) => {
				//下单
				let orderParams = {
					cart_id: that.cart_id,
					address_id: res.data,
					remark: ''
				}
				interfaceurl.checkAuth(interfaceurl.goodsPayment, orderParams).then((res) => {
					//保存成功后跳转到我的页面
					uni.switchTab({
					    url: '/pages/my/my'
					});
				});
			});
		}
	}
}
